*{-webkit-box-sizing:border-box;-moz-box-sizing:border-box;box-sizing:border-box}

html, body {
    height:100%;
    overflow: hidden;
}

section.x-article {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    font-size: 12pt;
    font-family: sans-serif;
    background-color: lightgoldenrodyellow;
}

section.x-article  a[href] {
    color: #a50;
    text-decoration: none;
}

section.x-article  a[href]:hover {
    text-decoration: underline;
}


section.x-article > header {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    width: 100%;
    height: 2em;
    line-height: 2em;
    background-color: #420;
    color: orange;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    padding: 0 .5em 0 0;
    box-shadow: 0 2px 1em #000;
    border-bottom: 1px solid #000;
}

section.x-article > header > a[href] {
    display: inline-block;
    width: 2em;
    text-align: center;
    margin: 0 1em 0 0;
    color: orange;
    border-right: 1px solid rgba(255,128,0,.6);
    text-shadow: 1px 1px #000;
}

section.x-article > header > a[href]:hover {
    background-color: orange;
    text-decoration: none;
    color: #420;
}



section.x-article > article {
    position: absolute;
    left: 0;
    top: 2em;
    right: 0;
    bottom: 2em;
    width: 100%;
    overflow: auto;
    padding: .5em;
    max-width: 60em;
}

section.x-article > footer {
    position: absolute;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 2em;
    background-color: orange;
    color: #000;
    text-align: center;
    overflow-x: auto;
    box-shadow: 0 -2px 1em #000;
    border-top: 1px solid #000;
}

section.x-article > footer > div {
    position: relative;
    display: table;
    height: 100%;
}

section.x-article > footer > div > div {
    display: table-cell;
    text-align: center;
    vertical-align: middle;
    height: 100%;
    width: 2em;
    margin: 0 2px;
}

section.x-article > footer > div > div > div {
    font-weight: bold;
    line-height: 1.6em;
}

section.x-article > footer > div > div > a[href] {
    display: inline-block;
    font-weight: normal;
    text-decoration: none;
    background: #840;
    color: #eee;
    height: 1.6em;
    width: 2em;
    line-height: 1.6em;
    border: 1px solid orange;
    box-shadow: 0 0 0 1px #840 inset;
}

section.x-article > footer > div > div > a:hover {
    background-color: #420;
}


section.x-article blockquote {
    border-left: .5em solid darkorange;
    padding-left: .5em;
    margin-left: .5em;
    font-size: 120%;
    font-weight: 300;
    line-height: 150%
}

pre.highlight {
    position: relative;
    margin: 4px;
    padding: 8px;
    background: #000;
    border: 2px solid #555;
    box-shadow: 0 0 5px #fff;
    color: #ccc;
    border-radius: .5em;
    overflow-x: auto;
}

pre.highlight.js::after {
    content: "Javascript";
    position: absolute;
    top: 0;
    right: 0;
    padding: 4px;
    font-variant: small-fonts;
    opacity: .3;
    color: #fff;
}

pre.highlight .keyword {
    color: #07f;
    font-weight: bold;
}

pre.highlight .keyword2 {
    color: #0ff;
    font-weight: bold;
}

pre.highlight .function {
    color: #fff;
    font-weight: bold;
}

pre.highlight .symbol {
    color: #f08;
}

pre.highlight .operator {
    color: #f80;
}

pre.highlight .comment {
    color: #777;
}

pre.highlight .string {
    color: #0f0;
}

pre.highlight .regexp {
    background: #ff8;
    color: #000;
}

